import React, {useState, useEffect} from "react";
import PropTypes from "prop-types";
import keycode from "keycode";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableFooter from "@material-ui/core/TableFooter";
import TableHead from "@material-ui/core/TableHead";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";
import TableSortLabel from "@material-ui/core/TableSortLabel";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import PlaylistPlayIcon from "@material-ui/icons/PlaylistAdd";
import CardEditMenu from './CardEditMenu.js'
import SearchBox from '../../../../../components/SearchBox/index';
import {useSelector} from 'react-redux';
import {useHistory} from 'react-router-dom';
import CircularProgress from '@material-ui/core/CircularProgress';
import {base_url,public_url, $http} from "config";
import Avatar from '@material-ui/core/Avatar';
import {NotificationContainer, NotificationManager} from 'react-notifications';

const columnData = [
  {id: "id", align: false, disablePadding: true, label: "ID"},
  {id: "task", align: true, disablePadding: false, label: "Task"},
  {id: "project_title", align: true, disablePadding: false, label: "Project Title"},
  {id: "site_id", align: true, disablePadding: false, label: "Site Name"},
  {id: "room", align: true, disablePadding: false, label: "Room"},
  {id: "survey_date", align: true, disablePadding: false, label: "Survey Date"},
  {id: "priority", align: true, disablePadding: false, label: "Priority"},
  {id: "archived", align: true, disablePadding: false, label: "Archived Day"},
  {id: "favourite", align: true, disablePadding: false, label: "Favourite"},
  {id: "actions", align: true, disablePadding: false, numeric: 'right', label: "Actions"}
];

const TaskTableHead = (props) => {

  const createSortHandler = property => event => {
    props.onRequestSort(event, property);
  };

    const {order, orderBy} = props;

    return (
      <TableHead>
        <TableRow>
          {columnData.map(column => {
            return (
              <TableCell
                key={column.id}
                align={column.numeric}
              >
                <Tooltip
                  title="Sort"
                  placement={column.numeric ? "bottom-end" : "bottom-start"}
                  enterDelay={300}
                >
                    <TableSortLabel
                    active={orderBy === column.id}
                    direction={order}
                    onClick={createSortHandler(column.id)}
                  >
                    {column.label}
                  </TableSortLabel>
                </Tooltip>
              </TableCell>
            );
          })}
        </TableRow>
      </TableHead>
    );
};

TaskTableHead.propTypes = {
  onRequestSort: PropTypes.func.isRequired,
  order: PropTypes.string.isRequired,
  orderBy: PropTypes.string.isRequired
};


let TaskTableToolbar = (props) => {
  const History = useHistory();

  const NewTask = () => {
    History.push('add-new');
  }

  const {SearchTask} = props;

  return (
    <Toolbar className="table-header">
      <div className="title">
        <Typography variant="h6">Tasks</Typography>
      </div>
      <div className="spacer">
          <div className="col-md-3 col-lg-3 col-sx-6 col-6 ml-auto">
              <SearchBox placeholder="Search ..."
                onChange={(event) => SearchTask(event.target.value)}
              />
          </div>
      </div>
      <div className="actions">
        <Tooltip title="New Task">
          <IconButton aria-label="New Task" onClick={()=>NewTask()}>
              <PlaylistPlayIcon/>
          </IconButton>
        </Tooltip>
      </div>
    </Toolbar>
  );
};

const TaskTable = () => {
  
  const [order, setOrder] = useState("asc");
  const [orderBy, setOrderBy] = useState("id");
  const [selected, setSelected] = useState([]);
  const [loader, setLoader] = useState(false);
  const {width} = useSelector(({settings}) => settings);
  const History = useHistory();

  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [tabledata, setTabledata] = useState([]);
  const [Tasks, setTasks] = useState([]);

  useEffect(() => {
    init_Data();
  }, []);

  const init_Data = () => {
    $http.get(base_url + "task/taskList", {params: {archived: 0}})
    .then((response) => {
      setTabledata(response.data.tasks);
      setTasks(response.data.tasks);
      setLoader(true);
    });
  }
  const onSetFavourite = (data) => {
    var fav_val = 0;
    fav_val = data.favourite ==1? 0 :1;
    $http.post(base_url + "task/setFavourite", {id: data.id,favourite:fav_val})
    .then((response) => {
      NotificationManager.info("You set favourite!");
      init_Data();
    });
  }
  const handleRequestSort = (event, property) => {

    let sort_order;
    if (orderBy === property && order === "asc") {
      sort_order = "desc";
    } else {
      sort_order = "asc";
    }

    
    sort_order === "desc"
      ? Tasks.sort((a, b) => (b[property] < a[property] ? -1 : 1))
      : Tasks.sort((a, b) => (a[property] < b[property] ? -1 : 1));

    
    setOrder(sort_order);
    setOrderBy(property);
  };


  const handleKeyDown = (event, id) => {
    if (keycode(event) === "space") {
      handleClick(event, id);
    }
  };

  const handleClick = (event, id) => {
    const selectedIndex = selected.indexOf(id);
    let newSelected = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, id);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1)
      );
    }
    setSelected(newSelected);
  };

  const handleChangePage = (event, page) => {
    setPage(page);
  };

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(event.target.value);
  };
  const onSelectTask = (data, index) => {
    if (index === 0) {
    //   History.push({
    //     pathname: "/app/tasks/company/info",
    //     state: {id: data}
    //   })
    } else if (index === 1) {
      History.push({
        pathname: "/app/task-manager/add-new",
        state: {id: data}
      })
    } else if (index === 2) {
      $http.post(base_url + "task/deleteTask", {id: data})
      .then((response) => {
        init_Data();
        NotificationManager.info("You removed selected task");
      })
    }
  }

  const SearchTask = (data) => {
    setTasks(
      tabledata.filter(function (item) {
        return (item.task.toLowerCase().includes(data.toLowerCase()));
      })
    );
  }

  return (
    
        <div>
              <TaskTableToolbar SearchTask={SearchTask}/>
          {
            loader ? (
              <div className="flex-auto">
                <div className="table-responsive-material">
                  <Table className="">
                    <TaskTableHead
                      order={order}
                      orderBy={orderBy}
                      onRequestSort={handleRequestSort}
                    />
                    <TableBody>
                      {Tasks.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((n) => 
                          <TableRow
                            hover
                            key={n.id}
                            // onClick={event => handleClick(event, n.id)}
                            onKeyDown={event => handleKeyDown(event, n.id)}
                            tabIndex={-1}
                          >
                            <TableCell>{n.id}</TableCell>
                            <TableCell>{n.task}</TableCell>
                            <TableCell>{n.project_name}</TableCell>
                            <TableCell>{n.site_name}</TableCell>
                            <TableCell>{n.room_number}</TableCell>
                            <TableCell>{n.due_by_date}</TableCell>
                            <TableCell>{n.priority?'yes':'no'}</TableCell>
                            <TableCell>{n.archived_day}</TableCell>
                            <TableCell>
                              <div className="user-profile d-flex flex-row align-items-center">
                                  <Avatar
                                      alt={n.account_manager}
                                      src={public_url + "upload/img/" + n.profile_pic}
                                      className="user-avatar"
                                  />
                                  <IconButton className="icon-btn" onClick={() => {
                                    onSetFavourite(n);
                                  }}>
                                    {n.favourite ==1 ?
                                      <i className="zmdi zmdi-star text-warning"/> :
                                      <i className="zmdi zmdi-star-outline text-warning"/>
                                    }

                                  </IconButton>
                              </div>
                            </TableCell>
                            <TableCell className="text-right">
                              <CardEditMenu key={n.id} selectedData={n.id} 
                                onSelectRow={onSelectTask}/>
                            </TableCell>
                          </TableRow>
                        )}
                    </TableBody>
                    <TableFooter>
                      <TableRow>
                        <TablePagination
                          count={Tasks.length}
                          rowsPerPage={rowsPerPage}
                          page={page}
                          onChangePage={handleChangePage}
                          onChangeRowsPerPage={handleChangeRowsPerPage}
                        />
                      </TableRow>
                    </TableFooter>
                  </Table>
                </div>
                <NotificationContainer/>
              </div>
            ):(
              <div className="loader-view"
                      style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
                  <CircularProgress/>
              </div>        
            )
          }
        </div>
  );
};

export default TaskTable;
